import React from 'react';
import { NavLink } from 'react-router-dom';

export const NavBar = () => {

  const NAVLINKS = [
    {
      to: '/',
      title: 'Home page',
    },
    {
      to: '/people',
      title: 'People page',
    },
  ];

  return (
  <nav className="navbar">
    <ul className="navbar-list">
        {NAVLINKS.map(navlink => (
          <li key={navlink.title} className="item">
            <NavLink
              className="nav-item"
              to={navlink.to}
              exact
              activeClassName="is-active"
            >
              {navlink.title}
            </NavLink>
          </li>
      ))}
    </ul>
    </nav>
  )
}